<template>
  <div>
    {{ support }}
  </div>
  <div>
    <q-btn color="primary" label="Get Notification" @click="captureImage" />
  </div>
  <div>
    Permission:  {{ respuesta }}
  </div>
</template>

<script>
import { useQuasar } from 'quasar'
import { ref } from 'vue'

export default {
  
  setup () {
    const $q = useQuasar()
    const respuesta =  ref('')
    const support = ref('')
    const options = {
      body: 'Esto es un ejemplo',
      tag: Date.now(),
      extra: {
        data: 'Algo mÃ¡s'
      }
    }

    Notification.requestPermission( res => {
      respuesta.value = res;
    })

    if (!('Notification' in window))
      support.value = "This browser does not support notifications.";
    else
      support.value = "This browser support notifications.";

    function captureImage () {
      if(Notification.permission === 'granted')
        navigator.serviceWorker.ready.then(reg => {
          $q.notify(options.body)
          reg.showNotification('Ejemplo', options);
        }).catch(err => {
          $q.notify(`Algo anda mal ${err}`)
        })
    }

    return {
      captureImage,
      respuesta,
      support,
    }
  }
}
</script>